import Api from './Api'
import Csrf from './Csrf'


export default {

  async login (form) {
    await Csrf.getCookie()
    return await  Api.post('../login', form)
  },

  async logout () {
    await Csrf.getCookie()

    return await Api.post('../logout')
  },

  async changePassword(form, id) {
    await Csrf.getCookie()

    return await Api.put(`changepassword/${id}`, form)
  },

  async create(form) {
    await Csrf.getCookie()

    return await Api.post('createnewuser', form)
  }

}
