<template>
  <div class="user-history">
    <section class="section">
      <div class="container">
        <div class="">
          <b-button
            type="is-success"
            label="New User"
            @click="activateNewUser"
            v-if="!activeNewUser"></b-button>
        </div>
        <template v-if="activeNewUser">
          <div class="box is-radiusless">
            <h1 class="has-text-primary is-size-4">New User</h1>
            <hr>
            <div class="columns is-multiline">
              <div class="column is-8">
                <b-field label="Full Name">
                  <b-input
                    v-model="form.fullName"
                    ></b-input>
                </b-field>
                <b-field label="Email">
                  <b-input
                    type="email"
                    v-model="form.email"
                    ></b-input>
                </b-field>
                <b-field label="Repeat Email">
                  <b-input
                    type="email"
                    v-model="form.repeatEmail"></b-input>
                </b-field>
                <b-field label="Password">
                  <b-input
                    type="password"
                    v-model="form.password"></b-input>
                </b-field>
                <b-field label="Repeat Password">
                  <b-input
                    type="password"
                    v-model="form.repeatPassword"></b-input>
                </b-field>
              </div>
              <div class="column is-4">
                <div class="bloquito">
                  <b-field>
                    <b-button
                      type="is-primary"
                      label="Create"
                      class="mr-2"
                      @click.prevent="createNewUser"></b-button>
                    <b-button
                      type="is-danger"
                      label="Cancel"
                      class="ml-2"
                      @click="cancelNewUser"></b-button>
                  </b-field>
                </div>
              </div>
            </div>
          </div>
        </template>
      </div>
    </section>
  </div>
</template>
<script>
import User from '@/api/User'
export default {
  name: 'UserHistory',
  data () {
    return {
      form: {
        fullName: null,
        email: null,
        repeatEmail: null,
        password: null,
        repeatPassword: null
      },
      activeNewUser: false,
      users: null
    }
  },
  methods: {
    activateNewUser: function () {
      this.activeNewUser = !this.activeNewUser
    },
    cancelNewUser: function () {
      this.form.fullName = null
      this.form.email = null
      this.form.repeatEmail = null
      this.form.password = null
      this.form.repeatPassword = null
      this.activeNewUser = !this.activeNewUser
    },
    createNewUser: function () {
      User.create(this.form).then((response) => {
        this.cancelNewUser()
        console.log(response)
      }).catch((errors) => {
        console.log(errors)
      })
    }
  }
}
</script>
